export const query = graphql`
  query {
    bioPicture: file(relativePath: { eq: "bio.png" }) {
      childImageSharp {
        fluid(maxWidth: 120) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

const smallIndex = {
  'fontSize': "3vw",
  'textAlign': "center"
};

const bioImage = {
  display: "inline-block",
};

const slideContent = (active_class) => {
  let id = document.getElementById(active_class);
  let a = document.getElementById("content-slider");
  a.style.height = id.clientHeight+"px";
  let oldElement = document.getElementsByClassName('active')[0]
  if (oldElement!==undefined){
    oldElement.classList.remove("active");
  }
}

